
form li,
form ul {
    list-style-type: none
}

form ul {
    margin: 0;
    padding: 0
}

fieldset {
    border: 0;
    padding: 0 0 2em;
    margin: 0
}

legend {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px
}

legend:active,
legend:focus {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: auto
}

.label,
label {
    text-transform: none;
    font-weight: 700;
    color: #333;
    font-size: 1.1em;
    display: block;
    padding: 0 0 .8em;
    margin: 0;
    line-height: 1.3em
}

.checkbox .label,
.checkbox label,
.radio .label,
.radio label {
    display: inline
}

.label.no-float,
label.no-float {
    float: none
}

@media screen and (min-width:50em) {
    .label,
    label {
        box-sizing: border-box;
        display: inline;
        float: left;
        width: 16.66667%;
        padding-right: 1.5%;
        padding-left: 1.5%;
        padding-top: 1.2em;
        padding-left: 0
    }
    .boolean_field .label,
    .boolean_field label,
    .image_field .label,
    .image_field label,
    .model_choice_field .label,
    .model_choice_field label,
    .model_multiple_choice_field .label,
    .model_multiple_choice_field label,
    .radio_select .label,
    .radio_select label {
        padding-top: 0
    }
    .model_choice_field.select .label,
    .model_choice_field.select label {
        padding-top: 1.2em
    }
}

.halloeditor,
.tagit,
input,
select,
textarea {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    box-sizing: border-box;
    border-radius: 6px;
    width: 100%;
    font-family: Open Sans, Arial, sans-serif;
    border: 1px solid #e6e6e6;
    padding: .9em 1.2em;
    background-color: #fafafa;
    color: #1a1a1a;
    font-size: 1.2em;
    font-weight: 300
}

.halloeditor:hover,
.tagit:hover,
input:hover,
select:hover,
textarea:hover {
    background-color: #fff
}

.halloeditor:focus,
.tagit:focus,
input:focus,
select:focus,
textarea:focus {
    background-color: #f2fcfc;
    border-color: #00b0b1
}

.halloeditor:disabled,
.halloeditor:disabled:hover,
.halloeditor[disabled],
.halloeditor[disabled]:hover,
.tagit:disabled,
.tagit:disabled:hover,
.tagit[disabled],
.tagit[disabled]:hover,
input:disabled,
input:disabled:hover,
input[disabled],
input[disabled]:hover,
select:disabled,
select:disabled:hover,
select[disabled],
select[disabled]:hover,
textarea:disabled,
textarea:disabled:hover,
textarea[disabled],
textarea[disabled]:hover {
    background-color: inherit;
    cursor: default;
    color: #e6e6e6
}

input[type=checkbox],
input[type=radio] {
    border-radius: 0;
    cursor: pointer;
    border: 0
}

input[type=radio] {
    height: 12px;
    width: auto;
    position: relative;
    margin-right: 27px
}

input[type=radio]:before {
    border-radius: 100%;
    font-family: wagtail;
    font-style: normal;
    text-align: center;
    position: absolute;
    top: -5px;
    left: -2px;
    cursor: pointer;
    display: block;
    content: "";
    width: 1em;
    height: 1em;
    line-height: 1.1em;
    padding: 4px;
    background-color: #fff;
    color: #e6e6e6;
    border: 1px solid #e6e6e6
}

input[type=radio]:checked:before {
    content: "";
    color: #007d7e
}

input[type=checkbox] {
    height: 12px;
    width: auto;
    position: relative;
    margin-right: 27px
}

input[type=checkbox]:before {
    font-family: wagtail;
    font-style: normal;
    text-align: center;
    position: absolute;
    top: -5px;
    cursor: pointer;
    display: block;
    content: "";
    line-height: 20px;
    width: 20px;
    height: 20px;
    background-color: #fff;
    border: 1px solid #e6e6e6;
    color: #fff
}

input[type=checkbox]:checked:before {
    color: #007d7e
}

body.ready fieldset,
body.ready input,
body.ready select,
body.ready textarea {
    transition: background-color .2s ease
}

.o-pill {
    padding: .2em .5em;
    border-radius: .25em;
    line-height: 1.5
}

.o-icon,
.o-pill {
    display: inline-block;
    vertical-align: middle
}

.o-icon {
    line-height: 1;
    margin-top: -.25rem
}

.avatar {
    border-radius: 100%;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    text-align: center;
    overflow: hidden;
    width: 50px;
    height: 50px
}

.avatar img {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    border: 0
}

.avatar.small {
    vertical-align: middle;
    margin: 0 .5em;
    width: 25px;
    height: 25px
}

.avatar.square,
.avatar.square:before {
    border-radius: 0
}

.c-transition-group {
    position: absolute;
    width: 100%;
    top: 0
}

.c-transition-push-enter {
    transform: translateX(100%);
    transition: transform .2s ease, opacity .2s linear;
    opacity: 0
}

.c-transition-push-enter-active,
.c-transition-push-leave {
    transform: translateX(0);
    opacity: 1
}

.c-transition-push-leave {
    transition: transform .2s ease, opacity .2s linear
}

.c-transition-pop-enter,
.c-transition-push-leave-active {
    transform: translateX(-100%);
    opacity: 0
}

.c-transition-pop-enter {
    transition: transform .2s ease, opacity .2s linear
}

.c-transition-pop-enter-active,
.c-transition-pop-leave {
    transform: translateX(0);
    opacity: 1
}

.c-transition-pop-leave {
    transition: transform .2s ease, opacity .2s linear
}

.c-transition-pop-leave-active {
    transform: translateX(100%);
    opacity: 0
}

.c-spinner:after {
    display: inline-block;
    animation: a .5s infinite linear;
    line-height: 1
}

.c-status {
    background: #333;
    text-transform: uppercase;
    letter-spacing: .03rem;
    font-size: 10px
}

.c-explorer__item {
    display: flex;
    flex-flow: row nowrap;
    border-bottom: 1px solid #333
}

.c-explorer__item__link {
    display: inline-flex;
    align-items: center;
    flex-grow: 1;
    padding: 1.45em 1em;
    cursor: pointer
}

.c-explorer__item__link:focus {
    background: rgba(0, 0, 0, .425);
    color: #fff
}

.c-explorer__item__link:hover {
    color: #fff
}

.no-touch .c-explorer__item__link:hover {
    background: rgba(0, 0, 0, .425)
}

@media screen and (min-width:50em) {
    .c-explorer__item__link {
        padding: 1.45em 1.75em
    }
}

.c-explorer__item__link .icon {
    font-size: 2em;
    color: #a5a5a5;
    margin-right: .5rem
}

.c-explorer__item__title {
    margin: 0;
    color: #fff;
    display: inline-block
}

.c-explorer__item__action {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    width: 50px;
    padding: 0 .5em;
