.c-explorer__item__action:focus {
    background: rgba(0, 0, 0, .425);
    color: #fff
}

.c-explorer__item__action:hover {
    color: #a5a5a5
}

.no-touch .c-explorer__item__action:hover {
    background: rgba(0, 0, 0, .425);
    color: #fff
}

.c-explorer__item__action .icon:before {
    margin-right: 0
}

.c-explorer__item__action--small {
    font-size: 1.2em
}

.c-explorer__meta {
    margin-left: .5rem;
    color: #a5a5a5;
    font-size: 12px
}

.explorer__wrapper,
.explorer__wrapper * {
    box-sizing: border-box
}

.explorer__wrapper {
    display: flex;
    flex: 1
}

.explorer {
    width: 100%;
    display: flex;
    flex-direction: column
}

@media screen and (min-width:50em) {
    .explorer {
        width: 485px;
        height: 100vh;
        position: fixed;
        z-index: 500;
        left: 200px
    }
}

.explorer:focus {
    outline-offset: -3px
}

.c-explorer {
    flex: 1;
    position: relative;
    overflow: hidden;
    background: #4c4e4d
}

@media screen and (min-width:50em) {
    .c-explorer {
        box-shadow: 2px 2px 5px rgba(0, 0, 0, .425)
    }
}

