    display: flex;
    flex-direction: column;
    height: 100%;
    z-index: 150
}

.c-explorer__close {
    padding: 1em;
    color: #a5a5a5;
    border-bottom: 1px solid hsla(0, 0%, 78%, .1);
    cursor: pointer
}

.c-explorer__close:focus {
    background-color: rgba(0, 0, 0, .425);
    color: #fff
}

.c-explorer__close:hover {
    color: #a5a5a5
}

@media screen and (max-width:49.9375em) {
    .explorer-open .c-explorer__close {
        display: block
    }
}

.c-explorer__drawer {
    flex: 1;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch
}

.c-explorer__header {
    display: block;
    background-color: #333;
    border-bottom: 1px solid #333;
    color: #fff
}

.c-explorer__header:focus {
    background-color: rgba(0, 0, 0, .425);
    color: #fff
}

.c-explorer__header:hover {
    color: #fff
}

.no-touch .c-explorer__header:hover {
    background-color: rgba(0, 0, 0, .425)
}

.c-explorer__header__inner {
    padding: 1em .75em;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap
}

.c-explorer__header__inner .icon {
    color: #a5a5a5;
    margin-right: .25rem;
    font-size: 1rem
}

@media screen and (min-width:50em) {
    .c-explorer__header__inner {
        padding: 1em 1.5em
    }
}

.c-explorer__placeholder {
    padding: 1em;
    color: #fff
}

@media screen and (min-width:50em) {
    .c-explorer__placeholder {
        padding: 1em 1.75em
    }
}

.c-explorer__see-more {
    display: block;
    padding: 1em;
    background: rgba(0, 0, 0, .3);
    color: #fff
}

.c-explorer__see-more:focus {
    color: #a5a5a5;
    background: rgba(0, 0, 0, .425)
}

.c-explorer__see-more:hover {
    color: #fff
}

.no-touch .c-explorer__see-more:hover {
    background: rgba(0, 0, 0, .425)
}

@media screen and (min-width:50em) {
    .c-explorer__see-more {
        padding: 1em 1.75em;
        height: 50px
    }
}

