.tab-nav {
    box-sizing: border-box;
    display: block;
    margin-right: auto;
    margin-left: auto;
    padding-right: 0;
    padding-left: 0;
    padding: 0;
    background: #e6e6e6
}

.tab-nav:after,
.tab-nav:before {
    content: " ";
    display: table
}

.tab-nav:after {
    clear: both
}

.tab-nav li {
    list-style-type: none;
    width: 33%;
    float: left;
    padding: 0;
    position: relative;
    margin-right: 1px
}

.tab-nav li:first-of-type {
    padding-left: 1.2em
}

.tab-nav h2 {
    margin: 0;
    font-size: inherit
}

.tab-nav a {
    background-color: #00676a;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1.2em;
    text-decoration: none;
    display: block;
    padding: .7em;
    color: #fff;
    border-top: .3em solid #00676a;
    max-height: 1.2em;
    overflow: hidden
}

body.ready .tab-nav a {
    transition: border-color .2s ease
}

.tab-nav a:hover {
    color: #fff;
    border-top-color: #003f41
}

.tab-nav a.errors:after {
    border-radius: 50px;
    box-shadow: 1px 2px 2px rgba(0, 0, 0, .1);
    position: absolute;
    right: -.5em;
    top: -.5em;
    z-index: 5;
    min-width: .9em;
    color: #fff;
    background: #cd3238;
    content: attr(data-count);
    padding: 0 .3em;
    line-height: 1.4em;
    text-align: center;
    font-size: .8em
}

.tab-nav li.active a {
    box-shadow: none;
    color: #333;
    background-color: #fff;
    border-top: .3em solid #333
}

.tab-nav li.settings a:before {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: wagtail;
    vertical-align: middle;
    text-transform: none;
    content: "";
    margin-right: .5em;
    font-size: 1.2em;
    font-weight: 400
}

.tab-nav.merged {
    margin-top: 0;
    background-color: #007d7e
}

.tab-content section {
    display: none;
    padding-top: 1em
}

.tab-content section.active {
    display: block
}

.page-locked .tab-content {
    cursor: not-allowed;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none
}

.page-locked .tab-content>* {
    pointer-events: none
}

@media screen and (min-width:50em) {
    .tab-nav.merged {
        background-color: #007d7e
    }
    .tab-nav li {
        width: auto;
        padding: 0;
        margin-left: .7em
    }
    .tab-nav a {
        padding-left: 40px;
        padding-right: 40px
    }
    .tab-nav li.settings a {
        padding-left: 2em;
        padding-right: 2em
    }
    .modal-content .tab-nav li {
        padding: 0;
        min-width: 0
    }
    .modal-content .tab-nav li:first-of-type {
        padding-left: 2.5em
    }
}

